@import "../theme/mixin";
@import "../base/mixins";
@import "../base/variables/font";
@import "../base/variables/link";
@import "normalize-css";
@import "variable";

// 
//  normalize.css 

//  html
//  1. https://developer.microsoft.com/en-us/microsoft-edge/platform/issues/7165383/
//  ie11  edge 
// 
// 2.  sans-serif
// 3. Windows PhoneiOS100%
// 4.  IOS 

html {
  -ms-overflow-style: scrollbar; // 1
  font-family: sans-serif; // 2
  font-weight: $font-weight-base;
  -webkit-text-size-adjust: 100%; // 3
  -ms-text-size-adjust: 100%; // 3
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); // 4
}

// IE10+ doesn't honor `<meta name="viewport">` in some cases.
/* stylelint-disable at-rule-no-vendor-prefix */
@at-root {
  @-ms-viewport {
    width: #{device-width};
  }
}

// 1. 
body {
  font-family: $font-family;
  //  rem  body 
  font-size: var(--font-size-base, $font-size-base);
  font-weight: $font-weight-base;
  line-height: $line-height-base;

  @include theme-prop(color, primary-on-light); // 1   rgba(0, 0, 0, .87),
  @include theme-prop(background-color, primary-on-dark); // 1  #fff
}

// normalize.scss  IE 9- 
dialog,
figcaption,
figure,
hgroup,
main {
  display: block;
}

// Credit: https://github.com/suitcss/base
//  tabindex  -1 
[tabindex="-1"]:focus {
  outline: none !important;
}

//
// 
//

/* stylelint-disable selector-list-comma-newline-after */
//  margin-top margin-bottom
h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: $header-margin-bottom;
}

// 
//  rem  margin-top margin-bottom
p {
  margin-top: 0;
  margin-bottom: $p-margin-bottom;
}

// Abbreviations
//
// 1. 
/* stylelint-disable selector-no-qualifying-type */
abbr[title] {
  cursor: help; // 1
}

address {
  margin-bottom: $address-margin-bottom;
  font-style: normal;
  line-height: inherit;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: $list-spacer;
}

ol,
ul {
  padding-left: $list-spacer;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  font-weight: $dt-font-weight;
}

dd {
  margin-bottom: $list-spacer / 2;
  margin-left: 0; // 
}

blockquote {
  margin: 0 0 1rem;
}

// Links

a {
  text-decoration: $link-decoration;

  @include theme-prop(color, primary);

  &:hover {
    text-decoration: $link-hover-decoration;

    @include theme-prop(color, primary-dark);
  }
}

// Code

pre {
  // 
  margin-top: 0;
  // 
  margin-bottom: $spacer;
  // 
  overflow: auto;
  //  IE11 and Edge 
  -ms-overflow-style: scrollbar;
}

figure {
  //  figure margin
  margin: 0 0 $spacer;
}

// 

img {
  // 
  vertical-align: middle;
}

/**
*  300ms 
*  touch-action: manipulation 
* 
* https://developer.mozilla.org/zh-CN/docs/Web/CSS/touch-action
* http://caniuse.com/#feat=css-touch-action
* http://patrickhlauke.github.io/touch/tests/results/#suppressing-300ms-delay
**/
a,
area,
button,
[role="button"],
input:not([type="range"]),
label,
select,
summary,
textarea {
  touch-action: manipulation;
}

//  Forms 

label {
  display: inline-block;
}

input,
button,
select,
optgroup,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"] {
  //  IOS 
  // See https://bugs.webkit.org/show_bug.cgi?id=139848
  // https://www.w3cplus.com/css3/changing-appearance-of-element-with-css3.html
  -webkit-appearance: listbox;
}

textarea {
  // 
  resize: vertical;
}

fieldset {
  //  0
  // https://html.spec.whatwg.org/multipage/#the-fieldset-and-legend-elements
  min-width: 0;
  // 
  margin: 0;
  padding: 0;
  border: 0;
}

legend {
  display: block;
  width: 100%;
  margin-bottom: $spacer / 2;
  font-size: $font-size-lg;
  line-height: inherit;
}

[type="search"] {
  // 
  -webkit-appearance: none;
}

//  output  display  inline-block
output {
  display: inline-block;
}

