// Tab 

$tab-padding-x: .75rem !default;
$tab-padding-y: 1.5rem !default;
$tab-font-size: 1rem !default;
$tab-font-weight: 400 !default;
$tab-line-height: 1.5rem !default;

// active 
$tab-active-width-subtract: 16px !default;
$tab-active-border: 2px !default;

// 
$tab-transition-duration: 240ms !default;

// Tab 
$tab-padding-x-lg: 1rem !default;
$tab-padding-y-lg: 2rem !default;
$tab-font-size-lg: 1.125rem !default;

$tab-padding-x-sm: .5rem !default;
$tab-padding-y-sm: 1rem !default;
$tab-font-size-sm: .875rem !default;

// 
$enable-custom-theme-for-tab: true !default;

// tab-bar-scroller
$tab-bar-scroller-indicator-width: 48px;
