// radio 
@import "../../ripple/mixin";
@import "../../theme/mixin";
@import "function";
@import "mixin";

// 
.radio {
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  border-radius: 50%;
  will-change: opacity, transform, border-color, background-color, color;
  cursor: pointer;

  // radio 
  &-background {
    position: absolute;
    display: inline-block;
    box-sizing: border-box;
    // 
    transition: radio-leave(opacity), radio-leave(transform);
    pointer-events: none;
  }

  //  radio  mixin
  @include radio-size();

  &-circle-outer {
    position: absolute;
    top: 0;
    left: 0;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    border: 2px solid;
    border-radius: 50%;
    // 
    transition: radio-leave(border-color);

    @include theme-prop(border-color, secondary-on-light);
  }

  // 
  &-circle-inner {
    position: absolute;
    top: 0;
    left: 0;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    transform: scale(0, 0);
    // 
    transition: radio-leave(transform), radio-leave(background-color);

    @include theme-prop(background-color, secondary-on-light);
  }

  &-native-control {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    opacity: 0;
    cursor: inherit;

    //  hover 
    &:hover,
    &:focus {
      + .radio-background {
        .radio-circle-outer {
          @include theme-prop(border-color, primary-on-light);
        }
      }
    }
  }
}

// checked  disabled 
.radio-native-control:checked,
.radio-native-control:disabled {
  + .radio-background {
    transition: radio-enter(opacity), radio-enter(transform);

    .radio-circle-outer {
      transition: radio-enter(border-color);
    }

    .radio-circle-inner {
      transition: radio-enter(transform), radio-enter(background-color);
    }
  }
}

// 
.radio-native-control:checked {
  + .radio-background {
    .radio-circle-outer {
      @include theme-prop(border-color, primary-on-light);
    }

    .radio-circle-inner {
      transform: scale(.5);

      @include theme-prop(background-color, primary-on-light);
    }
  }
}

//  ripple
.radio-ripple {
  .radio-background {
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      opacity: 0;
      transform: scale(0, 0);
      transition: radio-leave(opacity), radio-leave(transform);
      pointer-events: none;

      @include theme-prop(background-color, primary-on-light);
    }
  }

  // 
  .radio-native-control:focus {
    + .radio-background::before {
      opacity: .16;
      transform: scale(2, 2);
      transition: radio-enter(opacity), radio-enter(transform);
    }
  }
}

// disabled 
.radio-disabled {
  cursor: default;
  pointer-events: none;
}

.radio-native-control:disabled {
  + .radio-background {
    cursor: default;

    .radio-circle-outer {
      border-color: rgba(#000, .26);
    }

    .radio-circle-inner {
      background-color: rgba(#000, .26);
    }
  }
}

// 
@if ($enable-custom-theme and $enable-custom-theme-for-radio) {
  // 
  @each $theme in ("primary", "secondary") {
    .radio-#{$theme} {
      @include radio-theme($theme);
    }
  }

  //  radio
  @each $theme in $theme-assist {
    .radio-#{$theme} {
      @include radio-theme($theme, $opacity: .64, $assist: true);
    }
  }
}

// Sizing
.radio-lg {
  @include radio-size($radio-outline-size: $radio-outline-size-lg, $radio-size: $radio-size-lg);
}

.radio-sm {
  @include radio-size($radio-outline-size: $radio-outline-size-sm, $radio-size: $radio-size-sm);
}
