/* stylelint-disable declaration-block-trailing-semicolon, block-closing-brace-newline-before */

@mixin hover {
  &:hover { @content; }
}

@mixin hover-focus {
  &:focus,
  &:hover {
    @content;
  }
}

//  hover focus 
@mixin plain-hover-focus {
  &,
  &:focus,
  &:hover {
    @content;
  }
}

// hover focus active 
@mixin hover-focus-active {
  &:focus,
  &:active,
  &:hover {
    @content;
  }
}
