@mixin text-ellipsis() {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

//  todo 
// http://dev.mobify.com/blog/multiline-ellipsis-in-pure-css/
// http://www.cnblogs.com/accordion/p/6507320.html
// http://www.css88.com/archives/5206
@mixin text-ellipsis-line($line: 2) {
  // display: -webkit-box;
  -webkit-line-clamp: $line;
  -webkit-box-orient: vertical;
  overflow: hidden;
  word-break: break-all;
}
