// 
// https://material.io/guidelines/motion/duration-easing.html#duration-easing-common-durations
$animation-ease-out: cubic-bezier(0, 0, .2, 1) !default; //  deceleration-curve-timing
$animation-ease-in-out: cubic-bezier(.4, 0, .2, 1) !default; //  standard-curve-timing
$animation-ease-in: cubic-bezier(.4, 0, 1, 1) !default; //  acceleration-curve-timing
$animation-sharp: cubic-bezier(.4, 0, .6, 1) !default; //  sharp-curve-timing

// 
$animation-timing-function: (
  ease-out: $animation-ease-out,
  ease-in-out: $animation-ease-in-out,
  ease-in: $animation-ease-in-out,
  sharp: $animation-ease-in-out
);
