// Buttons variable
@import "../../base/variables/font";
@import "../../theme/variable";

$btn-height: 2rem; // 32px
$btn-min-width: 5rem !default;
$btn-padding-x: 1.25rem !default;
$btn-font-weight: 500 !default;

//  button ripple 
$btn-unfilled-ripple-opacity: .16;
$btn-filled-ripple-opacity: .32;

// btn 
$btn-height-lg: 2.5rem;
$btn-font-size-lg: $font-size-lg;

$btn-height-sm: 2rem;
$btn-font-size-sm: $font-size-sm;

// disabled 
$btn-bg-disabled: map-get($greys, 300) !default;

$btn-group-margin-x: .5rem !default;

// 
$enable-custom-theme-for-btn: true !default;
