// css min-width  max-width 
// 
//  3 min-width  max-width 

//  $name  xs 
// 
// @include media-breakpoint-up(xs) {
//  .test {
//    color: #ff0000;
//  }
// }
//
// 
//  .test {
//    color: #ff0000;
//  }
//

// @include media-breakpoint-up(sm) {
//  .test {
//    color: #ff0000;
//  }
// }
// 
// @media (min-width: 600px) {
//  .test {
//    color: #ff0000;
//  }
// }

@mixin media-breakpoint-up($name, $breakpoints: $responsive-breakpoints) {
  $min: breakpoint-min($name, $breakpoints);

  @if $min {
    @media (min-width: $min) {
      @content;
    }
  } @else {
    @content;
  }
}

//  media-breakpoint-up   $name  xl 
// 
// 
// @include media-breakpoint-down(xs) {
//  .test {
//    color: #ff0000;
//  }
// }
// 
// @media (max-width: 599px) {
//  .test {
//    color: #ff0000;
//  }
// }
//

// @include media-breakpoint-down(xl) {
//  .test {
//    color: #ff0000;
//  }
// }
// 
// .test {
//  color: #ff0000;
// }

@mixin media-breakpoint-down($name, $breakpoints: $responsive-breakpoints) {
  $max: breakpoint-max($name, $breakpoints);

  @if $max {
    @media (max-width: $max) {
      @content;
    }
  } @else {
    @content;
  }
}

// 
// @include media-breakpoint-between(xs, xl) {
//  .test {
//    color: #ff0000;
//  }
// }
// 
// @media (min-width: 599px) {
//  .test {
//    color: #ff0000;
//  }
// }
//
// @include media-breakpoint-between(sm, md) {
//  .test {
//    color: #ff0000;
//  }
// }
// 
// @media (min-width: 1023px) and (max-width: 1439px) {
//  .test {
//    color: #ff0000;
//  }
// }

@mixin media-breakpoint-between($lower, $upper, $breakpoints: $responsive-breakpoints) {
  $min: breakpoint-max($lower, $breakpoints);
  $max: breakpoint-max($upper, $breakpoints);

  @if ($max == null) {
    @media (min-width: $min) {
      @content;
    }
  } @else {
    @media (min-width: $min) and (max-width: $max) {
      @content;
    }
  }
}

//  sm  600px  1023px
//  breakpoint  xs  xl  xs  xl 
// @include media-breakpoint-only(xl) {
//  .test {
//    color: #ff0000;
//  }
// }
// 
//
// @include media-breakpoint-only(lg) {
//  .test {
//    color: #ff0000;
//  }
// }
// 
// @media (min-width: 1440px) and (max-width: 1599px) {
//  .test {
//    color: #ff0000;
//  }
// }

@mixin media-breakpoint-only($name, $breakpoints: $responsive-breakpoints) {
  $min: breakpoint-min($name, $breakpoints);
  $max: breakpoint-max($name, $breakpoints);

  @if $min != null and $max != null {
    @media (min-width: $min) and (max-width: $max) {
      @content;
    }
  } @else if $max == null {
    @include media-breakpoint-up($name) {
      @content;
    }
  } @else if ($min == null) {
    @include media-breakpoint-down($name) {
      @content;
    }
  }
}

//  $enable-breakpoint  false 
@mixin no-media-breakpoint() {
  @content;
}

