@import "../../theme/variable";

// switch  switch 
$switch-focus-ring-width: 48px !default;
$switch-focus-ring-height: 48px !default;
// switch 
$switch-knob-diameter: 20px !default;
// switch 
$switch-knob-vertical-offset: 3px !default;

// 
// switch 
$switch-track-width: $switch-focus-ring-width / 2 + $switch-knob-diameter / 2 !default;
// 
$switch-track-height: $switch-knob-diameter - $switch-knob-vertical-offset * 2 !default;
//  switch
$switch-control-active-translate-x: (min($switch-focus-ring-width, $switch-focus-ring-height) - $switch-knob-diameter) / 2 !default;
//  $switch-track-width - $switch-knob-diameter
$switch-knob-active-translate-x: ($switch-focus-ring-width - $switch-knob-diameter) / 2 !default;

// 
$switch-unchecked-track-color: #000 !default;
$switch-unchecked-knob-color: map-get($greys, 50) !default; // 
$switch-unchecked-focus-ring-color: map-get($greys, 500) !default; // 
$switch-disabled-knob-color: map-get($greys, 400) !default;// disabled

$switch-baseline-theme-color: primary-on-light !default;

// 
$enable-custom-theme-for-switch: true !default;

