// 

@import "./mixin";

// .animation-ease-out 
// .animation-ease-in-out 
// .animation-ease-in 
// .animation-sharp 
.animation {
  &-ease-out {
    @include animation-ease-out;
  }

  &-ease-in-out {
    @include animation-ease-in-out;
  }

  &-ease-in {
    @include animation-ease-in;
  }

  &-sharp {
    @include animation-sharp;
  }
}
