@import "../../base/variables/font";
@import "../../shadow/shadow";
@import "../../responsive/mixin";
@import "../../theme/mixin";
@import "variable";

// 
// 
// card-header card-body card-footer card-actions card-media

.card {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  border: 1px solid $card-border-color;
  border-radius: $card-border-radius;
  overflow: hidden;

  &-header,
  &-footer {
    padding: $card-spacing * .75 $card-spacing;
    background-color: $card-header-footer-bg;
  }

  &-header {
    border-bottom: 1px solid $card-border-color;
  }

  &-footer {
    border-top: 1px solid $card-border-color;
  }

  // 
  // 
  &-media-overlay {
    box-sizing: border-box;
    padding: $card-spacing;
  }

  &-media {
    width: 100%;
  }

  &-body {
    // https://developer.mozilla.org/zh-CN/docs/Web/CSS/flex
    // flex
    // 
    //  footers 
    flex: 1 1 auto;
    padding: $card-spacing;
  }

  &-title,
  &-subtitle {
    margin-bottom: $card-spacing * .75;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
  }

  &-title {
    font-size: $font-size-lg;
    font-weight: 500;
  }

  &-subtitle {
    margin-top: -$card-spacing / 2;
    font-size: $font-size-base;

    @include theme-prop(color, secondary-on-light);
  }

  &-text {
    margin: 0;
    font-size: $font-size-base;

    + .card-text {
      margin-top: $card-spacing / 2;
    }
  }

  &-actions {
    display: flex;
    box-sizing: border-box;
    padding: $card-spacing;

    .card-action {
      margin-right: $card-spacing;

      &:last-child {
        margin-right: 0;
      }
    }

    // 
    &-vertical {
      flex-flow: column;
      align-items: flex-start;

      .card-action {
        margin: 0 0 $card-spacing;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }

  // 
  &-avatar-wrap {
    position: relative;
    padding: $card-spacing;

    .card-avatar {
      position: absolute;
      width: 2.5rem;
      height: 2.5rem;
      border-radius: 50%;
    }

    .card-title {
      margin-left: 3.5rem;
    }

    .card-subtitle {
      margin-left: 3.5rem;
    }
  }

  &-block {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
    box-sizing: border-box;

    .card-media {
      width: auto;
      padding: $card-spacing;
    }
  }

  // card shadow
  &-shadow {
    border: 0;

    @include shadow(2);
  }

  // card tabs
  &-header-tabs {
    padding-top: 0;
    padding-bottom: 0;
  }
}

// padding-top: 0;
.card-avatar-wrap + .card-media,
.card-avatar-wrap + .card-body,
.card-avatar-wrap + .card-actions,
.card-body + .card-actions {
  padding-top: 0;
}

// 
.card-group {
  display: flex;
  flex-direction: column;

  > .card {
    margin-bottom: $card-spacing / 2;
  }

  @include media-breakpoint-up(sm) {
    flex-flow: row wrap;

    > .card {
      // https://developer.mozilla.org/zh-CN/docs/Web/CSS/flex
      // Flexbugs #4: https://github.com/philipwalton/flexbugs#4-flex-shorthand-declarations-with-unitless-flex-basis-values-are-ignored
      // flex-basis  bug
      flex: 1 0 0%;
      margin-bottom: 0;
      border-radius: 0;

      &:first-child {
        border-top-left-radius: $card-border-radius;
        border-bottom-left-radius: $card-border-radius;
      }

      &:last-child {
        border-top-right-radius: $card-border-radius;
        border-bottom-right-radius: $card-border-radius;
      }

      + .card {
        margin-left: 0;
        border-left: 0;
      }
    }
  }
}

// 
.card-spacing {
  display: flex;
  flex-direction: column;

  .card {
    margin-bottom: $card-spacing;
  }

  @include media-breakpoint-up(sm) {
    flex-flow: row wrap;
    margin-right: -$card-spacing;
    margin-left: -$card-spacing;

    .card {
      display: flex;
      // Flexbugs #4: https://github.com/philipwalton/flexbugs#4-flex-shorthand-declarations-with-unitless-flex-basis-values-are-ignored
      // flex-basis  bug
      flex: 1 0 0%;
      flex-direction: column;
      margin: 0 $card-spacing;
    }
  }
}

//  Columns 

.card-columns {
  .card {
    margin-bottom: $card-columns-margin;
  }

  @include media-breakpoint-up(sm) {
    column-count: $card-columns-count;
    column-gap: $card-columns-gap;

    .card {
      display: inline-flex; //  inline-block 
      width: 100%; // 
    }
  }
}
