// 
@import "../../animation/function";
@import "../../shadow/mixin";
@import "../../theme/mixin";
@import "../../responsive/mixin";
@import "variable";
@import "mixin";

// 
// .drawer / .drawer 
// 
// 1.  1200px 
// 
// 2.  1200px 
// 

$drawer-width: 240px;

.drawer {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 5; // todo
  box-sizing: border-box;
  width: 100%;
  overflow: hidden;
  pointer-events: none;
  // contain  Dom  Dom  widgets  contain  CSS 
  // https://developer.mozilla.org/zh-CN/docs/Web/CSS/contain
  contain: strict;

  // 
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .4);
    opacity: 0;
    opacity: var(--drawer-opacity, 0);
    will-change: opacity;
  }

  // 
  &-nav {
    position: absolute;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    overflow-x: hidden;
    overflow-y: auto;
    touch-action: none;

    @include shadow(16);
    // 
    @include drawer-slide;

    //  drawer nav 
    @each $breakpoint in map-keys($responsive-breakpoints) {
      $infix: breakpoint-infix($breakpoint, $responsive-breakpoints, $dash: false);

      @include media-breakpoint-up(#{$infix}) {
        width: 100%;
        max-width: map-get($drawer-breakpoints-width, #{$breakpoint});
      }
    }

    // 
    &:not(.drawer-animating) {
      transition: $drawer-transition-close;
    }
  }

  // header
  &-header {
    position: relative;
  }

  // 
  &-content {
    flex-grow: 1;
    box-sizing: border-box;
    margin: 0;
    padding-top: .5rem;
    padding-bottom: .5rem;
    overflow-x: hidden;
    overflow-y: auto;
    touch-action: pan-y;
    -webkit-overflow-scrolling: touch;
  }

  // 
  &-animating {
    &::before {
      transition: animation-enter(opacity, .3s);
    }

    .drawer-nav {
      transition: $drawer-transition-close;
    }

    &.drawer-open .drawer-nav {
      transition: $drawer-transition-open;
    }
  }

  // drawer  1200px 
  // @media screen and (max-width: 1199px)
  @include media-breakpoint-down(lg) {
    &-open {
      pointer-events: auto;

      &::before {
        opacity: 1;
        opacity: var(--drawer-opacity, 1);
      }

      .drawer-nav {
        @include theme-prop(background, background);
        @include drawer-slide-open;
      }
    }
  }

  &-main {
    transition: $drawer-main-padding-left;
    will-change: padding-left;
  }

  //  .icon-pure-menu
  // <div class="drawer-toggle"><i class="icon-pure-menu"></i></div>
  &-toggle {
    margin-right: 1rem;
    padding: .5rem 1rem .5rem 0;
    cursor: pointer;
  }
}

@include media-breakpoint-down(xs) {
  .drawer-toggle {
    position: absolute;
    left: 1rem;
  }
}
// drawer  1200px 
// @media screen and (max-width: 1199px)
@include media-breakpoint-down(lg) {
  .drawer-scroll-lock {
    height: 100vh;
    overflow: hidden;
  }
}

//  1200px drawer nav 
// @media screen and (min-width: 1199px)
@include media-breakpoint-up(lg) {
  $width: map-get($drawer-breakpoints-width, lg);

  .drawer {
    // 
    width: $width;
    pointer-events: auto;

    &-nav {
      border-right: 1px solid #e4e4e4;
      box-shadow: none;
      transform: none;
      transition: $drawer-transition-open;
    }

    &-content {
      @include theme-prop(background, background);
    }

    // 
    &-toggle:not(.drawer-permanent-toggle) {
      display: none;
    }

    &-main {
      padding-left: $width;
    }

    //  navbar 
    &-navbar-spacer {
      padding-left: $width;
    }

    //  margin-top  navbar 
    &.navbar-fixed-adjust .drawer-nav {
      margin-top: .4rem;
      padding-bottom: .4rem;
    }
  }
}

@include media-breakpoint-up(xl) {
  $width: map-get($drawer-breakpoints-width, xl);

  .drawer {
    // 
    width: $width;

    &-main {
      padding-left: $width;
    }

    //  navbar 
    &-navbar-spacer {
      padding-left: $width;
    }
  }
}

//  1200px  drawer-permanent
@include media-breakpoint-up(lg) {
  $width: map-get($drawer-breakpoints-width, lg);

  .drawer-permanent {
    width: $width;

    &.drawer-open {
      &::before {
        opacity: 0;
        opacity: var(--drawer-opacity, 0);
      }
    }

    // 
    &.drawer-close {
      left: -$width;
      transform: translateX(-$width);
      visibility: hidden;

      + .drawer-main {
        padding-left: 0;
      }
    }

    // 
    &.drawer-animating {
      transition: $drawer-transition-open;

      &.drawer-close {
        transition: $drawer-transition-close;
      }
    }
  }
}

@include media-breakpoint-up(xl) {
  $width: map-get($drawer-breakpoints-width, xl);

  .drawer-permanent {
    width: $width;

    &.drawer-close {
      left: -$width;
      transform: translateX(-$width);
    }
  }
}
